<template>
  <div class="wrap wide">
    <div class="wide">
      <img class="wide" src="@/assets/images/news_banner.png" alt="">
    </div>
    <div class="primary-content">
      <div class="timeAxis_standard timeAxis_skin_1 timeAxis_skin_1_status epidemic_news">
        <div class="timeAxis_row j_callST">
          <div class="timeAxis_row_const"></div>

          <div v-for="(item,index) in list" :key="index" class="timeAxis_row_flex">
            <div class="timeAxis_h2">

              <i class="ic_timeAxis_node">
                <i v-if="index !== 0" class="ic_timeAxis_node_cir"></i>
              </i>

              <b class="timeAxis_h2_txt" style="color: rgb(17, 17, 17);">{{item.title}}</b>
            </div>

            <div class="timeAxis_content">
              <span>{{item.item.info.title}}</span>
              <div v-for="(items,index) in item.item.info.covers" :key="index">
                <img v-if="index===0" :src="items.url" alt="">
              </div>
            </div>

            <div class="timeAxis_content_row"></div>
          </div>

        </div>
      </div>
    </div>

    <infinite-loading @infinite="infiniteHandler">
    </infinite-loading>
  </div>
</template>

<script>
import { ProtestFlash } from '@/api/indexCovid';
// 上拉加载更多
import InfiniteLoading from 'vue-infinite-loading';
export default {
  data() {
    return {
      page: 1,
      list: []
    }
  },
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    this.Protest();
  },
  components: {
    InfiniteLoading
  },
  methods: {
    async Protest($state) {
      let ProtestDAta = await ProtestFlash(this.page);
      let listData = ProtestDAta.data.components[1].data;
      if (!$state) return;
      if (listData.length) {
        this.page += 1;
        $state.loaded();
      } else {
        $state.complete();
      }
      this.list = [...this.list, ...listData]
    },
    infiniteHandler($state) {
      this.Protest($state);
    }
  },
}
</script>
<style lang="less" scoped>
.wrap {
  .primary-content {
    width: 100%;
    margin-top: -30px;
    background: #f8f8f8;
    border-radius: 30px 30px 0 0;
    position: relative;
    z-index: 4;
    border-top: 1px solid rgba(0, 0, 0, 0);

    .timeAxis_standard {
      padding: 30px 30px 0;
      position: relative;
      overflow: hidden;
      margin: 0 0 30px;

      .timeAxis_row {
        .timeAxis_row_flex {

          border-left: 2px solid hsla(0, 0%, 80%, .5) !important;
          padding-left: 18px !important;
          flex: 1 1;

          .timeAxis_h2 {
            color: #fe350e;
            font-weight: 700;
            margin-top: 10px;
            font-size: 24px;


            .ic_timeAxis_node {
              background: #357bdf !important;
              border-color: #357bdf !important;
              width: 20px;
              height: 20px;
              border-radius: 50%;
              position: absolute;
              left: 22px;
              display: flex;
              justify-content: center;
              align-items: center;

              .ic_timeAxis_node_cir {
                background-color: #FFF;
                width: 12px !important;
                height: 12px !important;
                border-radius: 50%;
              }
            }

            .timeAxis_h2_txt {
              color: #888;
              font-weight: 400;
            }
          }

          .timeAxis_content {
            font-size: 34px;
            background-color: #fff;
            padding: 20px;
            line-height: 1.3;
            display: flex;
            justify-content: space-between;
            align-items: center;

            span {
              display: block;
              flex: 1;
              margin-right: 10px;
            }

            img {
              flex: 1;
              width: 180px;
              height: 120px;
            }
          }

          .timeAxis_content_row {
            height: 20px;
            width: 100%;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
            -webkit-flex-flow: row wrap;
            -ms-flex-flow: row wrap;
            flex-flow: row wrap;
          }
        }
      }
    }
  }
}
</style>